import { useQuery } from "@apollo/client";
import React, { ComponentType, PropsWithChildren } from "react";
import { LoadingIndicator } from "../../../../lib/components";
import { REPORT } from "../../../../lib/graphql/queries";
import {
  Report as ReportData,
  ReportVariables,
} from "../../../../lib/graphql/queries/Report/__generated__/Report";

export const withReportPdfData = (
  WrappedComponent: ComponentType<
    PropsWithChildren<{ pdfData: string; closeReport: () => void }>
  >
) => {
  return ({
    children,
    userAnswerIds,
    closeReport,
  }: PropsWithChildren<{
    userAnswerIds: string[];
    closeReport: () => void;
  }>) => {
    // Get PDF data.
    const { data, loading } = useQuery<ReportData, ReportVariables>(REPORT, {
      variables: {
        userAnswerIds,
      },
      fetchPolicy: "no-cache",
    });

    if (loading || !data) {
      return <LoadingIndicator />;
    }

    if (data.report && data.report.pdf) {
      return (
        <WrappedComponent pdfData={data.report.pdf} closeReport={closeReport}>
          {children}
        </WrappedComponent>
      );
    }

    return null;
  };
};
