import {
  Card,
  CardContent,
  Chip,
  createStyles,
  Divider,
  Drawer,
  List,
  ListItem,
  ListItemIcon,
  ListItemText,
  makeStyles,
  Theme,
  Toolbar,
} from "@material-ui/core";
import DescriptionIcon from "@material-ui/icons/Description";
import AssignmentIndIcon from "@material-ui/icons/AssignmentInd";
import React from "react";
import { Viewer } from "../../lib/type";
import { useHistory, useLocation } from "react-router-dom";
import { PersonalInformation } from "./components";
import PeopleIcon from "@material-ui/icons/People";
import LocalHospitalIcon from "@material-ui/icons/LocalHospital";
import LinkIcon from "@material-ui/icons/Link";
import NoteIcon from "@material-ui/icons/Note";

const drawerWidth = 240;

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    drawer: {
      width: drawerWidth,
      flexShrink: 0,
      "@media (max-width:780px)": {
        display: "none",
      },
    },
    personalInfoCard: {
      margin: 5,
      overflow: "auto",
    },
    personalInfoCardContent: {
      "&:last-child": {
        paddingBottom: 16,
      },
    },
    personalInfoText: {
      fontWeight: 400,
      fontSize: "1.25rem",
      marginBottom: 10,
    },
    drawerPaper: {
      width: drawerWidth,
    },
    idChip: {
      marginTop: 3,
      marginBottom: 3,
    },
    adminMenu: {
      padding: 0,
    },
    adminMenuHeader: {
      height: 48,
      display: "flex",
      alignItems: "center",
      fontWeight: 500,
      fontSize: "1.125rem",
      paddingLeft: 10,
    },
    link: {
      color: "#111",
    },
  })
);

interface Props {
  viewer: Viewer;
  type: "doctor" | "user";
}

export const Sider = (props: Props) => {
  const classes = useStyles();
  const history = useHistory();
  const goToHome = () => {
    history.push("/user/home");
  };
  const goToReportPage = () => {
    history.push("/user/report");
  };
  const goToDoctorPage = () => {
    history.push(`/doctor/${viewer.userName}`);
  };
  const goToDoctorManagePage = () => {
    history.push(`/doctor/${viewer.userName}/manage`);
  };
  const location = useLocation();
  const { viewer, type } = props;

  const [width, setWidth] = React.useState(window.innerWidth);

  const updateWidth = () => {
    if (window.innerWidth !== width) setWidth(window.innerWidth);
  };

  React.useEffect(() => {
    window.addEventListener("resize", updateWidth);
    return () => window.removeEventListener("resize", updateWidth);
  });

  return (
    <Drawer
      className={classes.drawer}
      variant="permanent"
      classes={{ paper: classes.drawerPaper }}
      style={{
        display:
          type === "doctor" && window.innerWidth <= 1150 ? "none" : undefined,
      }}
    >
      <Toolbar />
      {type === "doctor" ? (
        <>
          <div className={classes.adminMenuHeader}>{viewer.name}医生已登入</div>
          <Divider />
          <List className={classes.adminMenu}>
            <ListItem button key="patientInfo" onClick={goToDoctorPage}>
              <ListItemIcon>
                <PeopleIcon
                  color={
                    location.pathname === `/doctor/${viewer.userName}`
                      ? "primary"
                      : "inherit"
                  }
                />
              </ListItemIcon>
              <ListItemText primary={"病人信息"} />
            </ListItem>
            <Divider />
            <ListItem button key="doctorInfo" onClick={goToDoctorManagePage}>
              <ListItemIcon>
                <LocalHospitalIcon
                  color={
                    location.pathname === `/doctor/${viewer.userName}/manage`
                      ? "primary"
                      : "inherit"
                  }
                />
              </ListItemIcon>
              <ListItemText primary={"医生信息"} />
            </ListItem>
            <Divider />
            <a href="/user" className={classes.link} target="_blank">
              <ListItem button key="doctorInfo">
                <ListItemIcon>
                  <LinkIcon />
                </ListItemIcon>
                <ListItemText primary={"患者端"} />
              </ListItem>
            </a>
            <Divider />
          </List>
        </>
      ) : (
        <>
          <Card variant="outlined" className={classes.personalInfoCard}>
            <CardContent className={classes.personalInfoCardContent}>
              <div className={classes.personalInfoText}>个人信息</div>
              <div className={classes.idChip}>
                <Chip
                  size="small"
                  icon={<AssignmentIndIcon />}
                  color="primary"
                  label={`门诊号：${viewer.userName}`}
                  variant="outlined"
                />
              </div>
              <PersonalInformation viewer={viewer} />
            </CardContent>
          </Card>
          <List>
            <ListItem button key="form" onClick={goToHome}>
              <ListItemIcon>
                <DescriptionIcon
                  color={
                    location.pathname === "/user/home" ? "primary" : "inherit"
                  }
                />
              </ListItemIcon>
              <ListItemText primary={"量表"} />
            </ListItem>
            <ListItem button key="report" onClick={goToReportPage}>
              <ListItemIcon>
                <NoteIcon
                  color={
                    location.pathname === "/user/report" ? "primary" : "inherit"
                  }
                />
              </ListItemIcon>
              <ListItemText primary={"报告"} />
            </ListItem>
          </List>
        </>
      )}
    </Drawer>
  );
};
