import {
  Dialog,
  DialogTitle,
  DialogContent,
  FormControlLabel,
  Checkbox,
} from "@material-ui/core";
import React from "react";

interface Props {
  isDialogOpen: boolean;
  handleDialogClose: () => void;
  additionalFields: string[];
  setAdditionalFields: (additionalFields: string[]) => void;
}

export const ADDITIONAL_FIELD_KEYS = [
  "depressed",
  "anxious",
  "biphasic",
  "schizophrenia",
] as const;
export const ADDITIONAL_FIELD_MODAL_DISPLAY_NAMES = [
  "抑郁障碍",
  "焦虑障碍",
  "双相障碍",
  "精神分裂症",
] as const;

export const removeElementFromArray = <T,>(array: T[], element: T) => {
  const newArray: T[] = [];
  for (let currentElement of array) {
    if (currentElement !== element) {
      newArray.push(currentElement);
    }
  }
  return newArray;
};

export const AdditionalFieldsModal = ({
  isDialogOpen,
  handleDialogClose,
  additionalFields,
  setAdditionalFields,
}: Props) => {
  const selectAdditionalField = (
    fieldKey: typeof ADDITIONAL_FIELD_KEYS[number],
    isSelected: boolean
  ) => {
    if (!isSelected) {
      setAdditionalFields(removeElementFromArray(additionalFields, fieldKey));
    } else if (!additionalFields.includes(fieldKey)) {
      setAdditionalFields([...additionalFields, fieldKey]);
    }
  };
  return (
    <Dialog
      open={isDialogOpen}
      onClose={handleDialogClose}
      aria-labelledby="form-dialog-title"
    >
      <DialogTitle id="form-dialog-title">高级筛选</DialogTitle>
      <DialogContent>
        {ADDITIONAL_FIELD_MODAL_DISPLAY_NAMES.map((displayName, index) => {
          return (
            <div key={displayName}>
              <FormControlLabel
                control={
                  <Checkbox
                    checked={additionalFields.includes(
                      ADDITIONAL_FIELD_KEYS[index]
                    )}
                    onChange={(event) => {
                      selectAdditionalField(
                        ADDITIONAL_FIELD_KEYS[index],
                        event.target.checked
                      );
                    }}
                  />
                }
                label={displayName}
              />
            </div>
          );
        })}
      </DialogContent>
    </Dialog>
  );
};
