import "@fontsource/noto-sans-sc/400.css";
import {
  ApolloClient,
  ApolloProvider,
  InMemoryCache,
  useMutation,
} from "@apollo/client";
import React, { useEffect, useRef, useState } from "react";
import ReactDOM from "react-dom";
import "./index.css";
import { DOCTOR_LOG_IN } from "./lib/graphql/mutations";
import { Doctor, Patient, Viewer } from "./lib/type";
import reportWebVitals from "./reportWebVitals";
import {
  BrowserRouter as Router,
  Redirect,
  Route,
  Switch,
} from "react-router-dom";
import {
  AppHeader,
  Sider,
  Home,
  Login,
  Form,
  Report,
  PatientInformation,
  Welcome,
  PromptMessage,
  PatientLogin,
  CreateDoctor,
} from "./sections";
import {
  createMuiTheme,
  createStyles,
  makeStyles,
  Theme,
  ThemeProvider,
  Toolbar,
} from "@material-ui/core";
import { PrivateRoute } from "./lib/components/PrivateRoute";
import { LoadingIndicator } from "./lib/components";
import { zhCN } from "@material-ui/core/locale";
import {
  DoctorLogIn,
  DoctorLogInVariables,
} from "./lib/graphql/mutations/DoctorLogIn/__generated__/DoctorLogIn";
import {
  PatientLogIn,
  PatientLogInVariables,
} from "./lib/graphql/mutations/PatientLogIn/__generated__/PatientLogIn";
import { PATIENT_LOG_IN } from "./lib/graphql/mutations/PatientLogIn";
import "fontsource-roboto";

const client = new ApolloClient({
  uri: "/api",
  cache: new InMemoryCache(),
  headers: { "X-CSRF_TOKEN": sessionStorage.getItem("token") || "" },
});

const initialViewer: Viewer = {
  userName: null,
  token: null,
  didRequest: false,
  catelogy: null,
};

const initialDoctor: Doctor = {
  name: null,
  userName: null,
  didRequest: false,
};

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      display: "flex",
      height: "100%",
    },
    contentContainer: {
      flexGrow: 1,
      height: "100%",
    },
  })
);

export const PRIMARY_COLOR = "#1a73e8";
const App = () => {
  const classes = useStyles();

  // const [viewer, setViewer] = useState<Viewer>(initialViewer);
  const [doctor, setDoctor] = useState<Doctor>(initialDoctor);
  const [patient, setPatient] = useState<Patient>(initialViewer);
  // const [logIn] = useMutation<LogInData, LogInVariables>(LOG_IN, {
  //   onCompleted: (data) => {
  //     if (data && data.logIn) {
  //       setViewer(data.logIn);
  //       if (data.logIn.token) {
  //         sessionStorage.setItem("token", data.logIn.token);
  //       } else {
  //         sessionStorage.removeItem("token");
  //       }
  //     }
  //   },
  // });

  // const logInRef = useRef(logIn);
  const [doctorLogIn, { loading: doctorLogInLoading }] = useMutation<
    DoctorLogIn,
    DoctorLogInVariables
  >(DOCTOR_LOG_IN, {
    onCompleted: (data) => {
      if (data && data.doctorLogIn) {
        const { name, userName, didRequest } = data.doctorLogIn;
        setDoctor({
          name,
          userName,
          didRequest,
        });
      }
    },
  });
  const doctorLogInRef = useRef(doctorLogIn);

  const [patientLogIn, { loading: patientLogInLoading }] = useMutation<
    PatientLogIn,
    PatientLogInVariables
  >(PATIENT_LOG_IN, {
    onCompleted: (data) => {
      if (data && data.patientLogIn) {
        setPatient(data.patientLogIn);
      }
    },
  });

  const patientLogInRef = useRef(patientLogIn);
  useEffect(() => {
    doctorLogInRef.current();
    // logInRef.current();
    patientLogInRef.current();
  }, []);
  if (
    doctorLogInLoading ||
    !doctor.didRequest ||
    patientLogInLoading ||
    !patient.didRequest
  ) {
    return <LoadingIndicator />;
  }
  const isAuthorized = !!doctor.userName;
  return (
    <Router>
      {/** App Header */}
      <Switch>
        <Route exact path="/" />
        <Route exact path="/user/prompt" />
        <Route exact path="/user/login" />
        <Route path="/user/home" />
        <Route path="/doctor">
          <AppHeader viewer={doctor} setViewer={setDoctor} type="doctor" />
          <Switch>
            <Route exact path="/" />
          </Switch>
        </Route>
      </Switch>

      <Switch>
        <Route exact path="/login">
          <Login
            isAuthorized={isAuthorized}
            setViewer={setDoctor}
            setDoctor={setDoctor}
            doctor={doctor}
          />
        </Route>
        <Route
          exact
          path="/doctor"
          render={({ location }) => (
            <Redirect to={{ pathname: "/login", state: { from: location } }} />
          )}
        />

        <PrivateRoute isAuthorized={isAuthorized} path="/user" exact>
          <Welcome
            setDoctor={setDoctor}
            doctor={doctor}
            logIn={doctorLogInRef.current}
          />
        </PrivateRoute>
        <PrivateRoute isAuthorized={isAuthorized} path="/user/prompt" exact>
          <PromptMessage />
        </PrivateRoute>
        <PrivateRoute isAuthorized={isAuthorized} path="/user/login" exact>
          <PatientLogin setPatient={setPatient} patient={patient} />
        </PrivateRoute>
        {doctor.userName && (
          <PrivateRoute
            isAuthorized={isAuthorized}
            path={`/doctor/${doctor.userName}`}
          >
            <div className={classes.root}>
              <Sider viewer={doctor} type="doctor" />
              <div className={classes.contentContainer}>
                <Toolbar />
                <Switch>
                  <Route
                    exact
                    path={`/doctor/${doctor.userName}`}
                    component={PatientInformation}
                  />
                  <Route
                    exact
                    path={`/doctor/${doctor.userName}/manage`}
                    component={CreateDoctor}
                  />
                </Switch>
              </div>
            </div>
          </PrivateRoute>
        )}
        {patient.userName && (
          <PrivateRoute isAuthorized={isAuthorized} path="/user">
            <AppHeader viewer={patient} setViewer={setPatient} type="user" />
            <Switch>
              <PrivateRoute isAuthorized={isAuthorized} path="/user/form" exact>
                <Form />
              </PrivateRoute>

              <PrivateRoute isAuthorized={isAuthorized} path="/user">
                <div className={classes.root}>
                  <Sider viewer={patient} type="user" />
                  <div className={classes.contentContainer}>
                    <Toolbar />
                    <Switch>
                      <Route exact path="/user/home">
                        <Home patient={patient} />
                      </Route>
                      <Route exact path="/user/report" component={Report} />
                    </Switch>
                  </div>
                </div>
              </PrivateRoute>
            </Switch>
          </PrivateRoute>
        )}
        <PrivateRoute isAuthorized={isAuthorized} path="/user">
          <></>
        </PrivateRoute>
        <PrivateRoute isAuthorized={isAuthorized} path="/doctor">
          <></>
        </PrivateRoute>
        <Route exact path="/">
          <Redirect to="/user" />
        </Route>
      </Switch>
    </Router>
  );
};

const theme = createMuiTheme(
  {
    palette: {
      primary: {
        light: "#6a9dff",
        main: PRIMARY_COLOR,
        dark: "#0045b5",
        contrastText: "#fff",
      },
    },
  },
  zhCN
);
ReactDOM.render(
  <ApolloProvider client={client}>
    <ThemeProvider theme={theme}>
      <App />
    </ThemeProvider>
  </ApolloProvider>,
  document.getElementById("root")
);

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();
